// pages/index.js
import { useState } from "react";

export default function Home() {
  const [query, setQuery] = useState("");
  const [loading, setLoading] = useState(false);
  const [results, setResults] = useState([]);
  const [error, setError] = useState("");

  async function handleSearch(e) {
    e.preventDefault();
    if (!query.trim()) return;
    setLoading(true);
    setError("");
    setResults([]);

    try {
      const res = await fetch("/api/suggest", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ product_name: query }),
      });

      const data = await res.json();
      if (!res.ok) throw new Error(data.error || "Request failed");
      setResults(data.suggestions || []);
    } catch (err) {
      setError(err.message);
    } finally {
      setLoading(false);
    }
  }

  return (
    <div style={{ fontFamily: "Inter, sans-serif", padding: 24, maxWidth: 900, margin: "0 auto" }}>
      <h1 style={{ color: "#2ecc71", fontSize: "2rem", marginBottom: 16 }}>ðŸŒ¿ EcoSage</h1>
      <p>Find eco-friendly alternatives to everyday products powered by AI.</p>

      <form onSubmit={handleSearch} style={{ display: "flex", gap: 8, marginBottom: 16 }}>
        <input
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="e.g., plastic toothbrush"
          style={{ flex: 1, padding: "12px", borderRadius: 6, border: "1px solid #ddd" }}
        />
        <button
          type="submit"
          style={{ background: "#2ecc71", color: "white", padding: "12px 18px", border: "none", borderRadius: 6 }}
        >
          {loading ? "Searching..." : "Search"}
        </button>
      </form>

      {error && <p style={{ color: "red" }}>{error}</p>}

      <div>
        {results.map((s, i) => (
          <div key={i} style={{ border: "1px solid #eee", borderRadius: 8, padding: 16, marginBottom: 12 }}>
            <h3>{s.name}</h3>
            <p style={{ color: "#2ecc71" }}>{s.reason}</p>
            <p>{s.description}</p>
            <a
              href={`https://www.google.com/search?q=${encodeURIComponent(s.name)}`}
              target="_blank"
              rel="noopener noreferrer"
              style={{
                display: "inline-block",
                marginTop: 8,
                background: "#0b7285",
                color: "white",
                padding: "8px 12px",
                borderRadius: 6,
                textDecoration: "none",
              }}
            >
              Find Online
            </a>
          </div>
        ))}
      </div>
    </div>
  );
}
